import styled from 'styled-components' 

export const Container = styled.form`
padding: 30px ;

P{
    display: block ;
}
h2{
    display: block ;
    margin-bottom: 5px ;
    color: rgb(91, 24, 153) ;
}
label{
    display: block ;
    margin-top:20px;
    margin-bottom:5px;
}

a{
    padding: 0 15px 0 15px ;
    text-decoration:none ;
    color:rgb(91, 24, 153) ;
    margin-top: 2rem;
    display:block;
}

.passo{
    margin-bottom: 20px ;
}
input {
            display: block;
            margin-top: 7px;
            box-sizing: border-box;
            width: 100%;
            padding: 15px 10px;
            border: 2px solid rgb(91, 24, 153);
            border-radius: 10px;
            font-size: 28px;
            outline: 0;
            font-size: 15px;
            background-color: rgb(235, 235, 235);
        }

        .navigation{
            display:flex;
            align-items:center;
        }

`